import React from 'react'
import SerachBar from '../components/SerachBar'
import VideoList from '../components/VideoList'
import VedioPlayer from '../components/VedioPlay'
import {connect} from 'react-redux'
import {fetchDefultNews} from '../action'

class App extends React.Component{
    state={vidid:""}
    componentDidMount(){
       this.props.fetchDefultNews("news")
    }
   
     getDefultNews=() =>{
         if(!this.props.newsVideo){
             return<div className="text-center">
             <p className='text-center text-danger'>Sorry ..if video search is not listing results it probably due to youtube  API restiction that excceds SERACH quta limit </p>
         <div className="spinner-border text-dark" role="status">
         
         </div>
         </div>
         }
         return(
             
         <div>
             {this.props.newsVideo.map(x=>
                      <VideoList src={x.snippet.thumbnails.medium.url} title={x.snippet.description} videioid={x.id.videoId} chanalid={x.snippet.channelTitle} publishedat={x.snippet.publishedAt} />
                        
                        )}
         </div>

         )}
         getSearchResults=()=>{
             return(
                 <div className='row'>
                     <div className='col-md-12 text-center' ><h6 className=' text-info'>{this.props.videos.length===0?"":"Video Search Result"}</h6></div>  
                    {this.props.videos.map(x=>
                      <VideoList src={x.snippet.thumbnails.medium.url} title={x.snippet.description} videioid={x.id.videoId} chanalid={x.snippet.channelTitle} publishedat={x.snippet.publishedAt} />
                        
                        )}
                    
                 </div>
             )
         }
        
    
     render(){
       
        return(
            <div className='container' style={{paddingTop:"5px"}}  >
                <div className='row'>
                    <div className='col-md-12 ' >
                    <SerachBar/>
                    </div>
        

                </div>
                <br/>
                <div className='row '  >
                    <div className='col-md-7'  >
                    <VedioPlayer /> 
                    </div> 
                    <div  className='  col-md-5 '  style={{height:"90vh",overflow:"scroll",paddingLeft:"50px",background:"gainsbro"}}  >
                    {this.props.videos.length===0?this.getDefultNews():this.getSearchResults()}
                    </div>

                </div>
               
            </div>
        )
    }
}
const mapStateToProps=(state)=>{
    return{videos:state.fetcheVideosReducers,newsVideo:state.getNewsReducer}
}
export default connect(mapStateToProps,{fetchDefultNews}) (App)